.header-wrapper {
    box-sizing: border-box;
    width: 50%;
    margin: auto;
    margin-top: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    /* border: 5px solid black; */
}

.header-text {
    /* font-family: 'Playfair Display', serif; */
    /* font-family: 'Quicksand', sans-serif; */
    /* font-family: 'Cinzel', serif; */
    /* font-family: 'Poiret One', cursive; */
    /* font-family: 'Vidaloka', serif; */
    font-family: 'Syne', sans-serif;
    font-size: x-large;
    width: 100%;
    order: 1;
}

.header-sub {
    font-family: 'Syne', sans-serif;
    font-size: x-large;
    color: white;
    text-shadow: -1.5px 0 black, 0 1.5px black, 1.5px 0 black, 0 -1.5px black;
    width: 100%;
    order: 1;
}

.header-pic {
    height: 35vmin;
    padding: 2px;
    order: 0;
}

.navigation-wrapper {
    box-sizing: border-box;
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: row;
    padding-top: 0px;
    /* padding: 10px; */
    padding-bottom: 10px;
    justify-content: flex-end;
    /* justify-content: space-evenly; */
    align-items: center;
    /* border: .5px solid black; */
}

.navigation-border {
    height: 13vmin;
    align-self: flex-start;
    float: none;
    order: 0;
}


.total-wrapper {
    display: flex;
    justify-content: center;
    width: 100%; 
    background-color: #E3D8D3;
}

.about-wrapper{
    display: flex;
    margin: auto;
    justify-content: space-between;
    flex-direction: row;
    width: 50%;
    /* border: 1px solid black; */

}

.links-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 20%;
    height: 80%;
    padding: 20px;
    /* border: 1px solid black; */
}

